import { Message, messagesDatabase } from "../datastore/messagesDatabase";
import { VideoComment } from "./fb.definitions";

function videoCommentToDbMessage(message: VideoComment, videoId: string, pageId: string): Required<Message> {
  return {
    id: message.id.toString(),
    message: message.message,
    createdTime: message.created_time,
    user: message.from.name,
    pageId,
    userPicture: message.from.picture.data.url,
    userId: message.from.id,
    broadcastTimestamp: message.live_broadcast_timestamp,
    videoId,
    likeCount: message.like_count
  }
}

export function dbMessageToVideoComment(message: Message): VideoComment {
  return {
    id: message.id ?? '',
    message: message.message ?? 'Unknown Message',
    created_time: message.createdTime ?? new Date().toISOString(),
    like_count: message.likeCount ?? 0,
    live_broadcast_timestamp: message.broadcastTimestamp ?? 0,
    from: {
      id: '',
      name: message.user ?? 'Unknown Name',
      picture: {
        data: {
          url: message.userPicture ?? 'data:image/jpeg;base64,'
        }
      }
    }
  }
}

export async function batchInsertApiMessages(messages: VideoComment[], videoId: string, pageId: string) {
  const dbMessages = messages.map((m) =>  videoCommentToDbMessage(m, videoId, pageId))
  return await messagesDatabase.messages.bulkAdd(dbMessages)
}

export async function insertOrUpdateMessage(message: VideoComment, videoId: string, pageId: string) {
  const {id, ...allMessage} = videoCommentToDbMessage(message, videoId, pageId)
  if(await messagesDatabase.messages.get(id) === undefined) {
    return await messagesDatabase.messages.add({id, ...allMessage})
  } else {
    return await messagesDatabase.messages.update(id, allMessage)
  }
}
