import createPersistedState from "use-persisted-state"
import { PageTokensResponse } from "../definitions"
import { FacebookPaginatedResponse } from "./fb.definitions"
import { fetchStreamlabsConfig } from "./streamlabs"

export class InvalidFacebookUserTokenError extends Error {}
export class InvalidFacebookPageTokenError extends Error {}


export interface FacebookMe {
  name: string
}

export interface HasError {
  error: any
}

export async function getUserToken(): Promise<string> {
  const streamlabsConfig = await fetchStreamlabsConfig()
  return streamlabsConfig.oauths.facebook_account
}

export async function getUserId(): Promise<string> {
  const streamlabsConfig = await fetchStreamlabsConfig()
  return streamlabsConfig.usernames.facebook_account
}

export async function getPagesTokens(): Promise<PageTokensResponse> {
  const pageAccessTokensResponse = await fetch(`https://graph.facebook.com/${await getUserId()}/accounts?access_token=${await getUserToken()}`)
  return pageAccessTokensResponse.json() 
}

export async function getPageToken(id: string) {
  const tokens = await getPagesTokens()
  return tokens.data.find(t => t.id === id)
}

export type PersistedVideoData = Record<string, boolean>
export const usePersistedVideoData = createPersistedState('persistedVideoData')

interface FbError {
  error: string | unknown
}

export interface FbParams {
  access_token: string;
  fields: string;
  limit?: number
  live_filter?: 'no_filter' | 'filter_low_quality'
  order?: 'chronological' | 'reverse_chronological'
  since?: string
}

export function FbApiCall<T = unknown>(path: string, params: FbParams, method: "get" | "post" | "delete" = 'get'): Promise<T> {
  return new Promise((resolve, reject) => {
    FB.api(
      path,
      method,
      params,
      function (response: T | FbError | undefined) {
        if(response == null) {
          reject(new Error('Empty Response'))
        }
        if((response as FbError).error != null) {
          const errorResponse = response as FbError
          const message = typeof errorResponse.error === 'string' ? errorResponse.error : 'FB Api Error'
          console.error(response)
          reject(new Error(message))
        }
        resolve(response as T)
      }
    );
  })
}

export async function getAllResults<T extends FacebookPaginatedResponse>(results: T) {
  let allResults: T['data'] = []

  allResults.push(...results.data)
  while (results.paging?.next !== undefined) {
    const nextResponse = await fetch(results.paging.next)
    results = await nextResponse.json()
    allResults.push(...results.data)
  }
  return allResults
}