import { ArrowDownOutlined, ArrowUpOutlined, EyeInvisibleOutlined, EyeOutlined } from '@ant-design/icons'
import { List, Pagination, Comment, Tooltip } from 'antd'
import { DateTime } from 'luxon'
import React, { createElement, FC, useState } from 'react'
import { useParams } from 'react-router-dom'
import { VideoComment } from '../util/fb.definitions'
import { COMMENTS_PER_PAGE, CustomMessageState, CustomVideoState, defaultVideoCustomState, useVideoCustomState } from './util'

interface NotifiesListProps {
  comments: VideoComment[]
  perPage: number
  text: string
}

function notifyActions(state: CustomMessageState, setMessageState: (messageState: Partial<CustomMessageState>) => void) {
  return [
    <Tooltip key="comment-basic-position" title={state.down ? 'Move Up' : 'Move Down'}>
      <span onClick={() => {setMessageState({down: !state.down})}}>
        {createElement(state.down ? ArrowUpOutlined : ArrowDownOutlined)}
      </span>
    </Tooltip>,
    <Tooltip key="comment-basic-hide" title={state.hidden ? 'Show' : 'Hide'}>
      <span onClick={() => {setMessageState({hidden: !state.hidden})}}>
        {React.createElement(state.hidden ? EyeOutlined : EyeInvisibleOutlined)}
      </span>
    </Tooltip>
  ]
}

export const defaultMessageState:CustomMessageState = {hidden: false, down: false}

function formatNotifies(comments: VideoComment[], videoState: CustomVideoState, setVideoState: React.Dispatch<React.SetStateAction<CustomVideoState>>) {
  const setMessageState = (messageId: string) => (messageState: Partial<CustomMessageState>) => {
    setVideoState((prevState) => {
      const currentMessageState = prevState.messages[messageId] ?? defaultMessageState
      const newMessageState = {...currentMessageState, ...messageState}
      return {...prevState, messages: {...prevState.messages, [messageId]: newMessageState}}
    })
  }
  return comments.map((c) => {
    const date = DateTime.fromISO(c.created_time)
    const messageState = videoState.messages[c.id] ?? defaultMessageState
    return {
      actions: notifyActions(messageState, setMessageState(c.id)),
      author: c.from.name,
      avatar: c.from.picture.data.url,
      content: <p>{c.message}</p>,
      datetime: (
        <Tooltip title={date.toLocaleString(DateTime.TIME_WITH_SECONDS)}>
          <span>{date.toRelative()}</span>
        </Tooltip>
      )
    }
  })
}

export const NotifiesList: FC<NotifiesListProps> = (props) => {
  const { comments, perPage, text } = props
  const { id } = useParams<{ id: string }>()
  const [currentPage, setPage] = useState(1)
  const [videoState, setVideoState] = useVideoCustomState(id)(defaultVideoCustomState)
  const offset = (currentPage - 1) * perPage
  return (
    <>
      <List
        className="comment-list"
        header={`${comments.length} ${text}`}
        itemLayout="horizontal"
        dataSource={formatNotifies(comments.slice(offset, offset + perPage), videoState, setVideoState)}
        renderItem={(item) => (
          <li>
            <Comment actions={item.actions} author={item.author} avatar={item.avatar} content={item.content} datetime={item.datetime} />
          </li>
        )}
      />
      <Pagination
        current={currentPage}
        onChange={(p) => setPage(p)}
        total={comments.length}
        showSizeChanger={false}
        defaultPageSize={COMMENTS_PER_PAGE}
        showTotal={(total, range) => `${range[0]}-${range[1]} of ${total} ${text}`}
        defaultCurrent={1}
      />
    </>
  )
}

export function generateNotifiesForRound(videoId: string, notifies: VideoComment[], currentState: CustomVideoState) {
  const withoutHiddenAndMoved = notifies.filter(n => !(currentState.messages[n.id] ?? defaultMessageState).hidden).filter(n => !(currentState.messages[n.id] ?? defaultMessageState).down)
  const movedDown = notifies.filter(n => (currentState.messages[n.id] ?? defaultMessageState).down)
  return [...withoutHiddenAndMoved, ...movedDown]
}