import { last } from 'lodash'
import createPersistedState from 'use-persisted-state'
import { StreamLabsConfig } from './streamlabs.definitions'

export class InvalidChatboxUrlError extends Error {}

export const useChatBoxUrlState = createPersistedState('chatboxUrl')

const STARTS = 'https://streamlabs.com/widgets/chat-box/v1/'
export function isValidUrl() {
  const storedUrl = JSON.parse(window.localStorage.getItem('chatboxUrl') ?? '""')
  console.log('storedUrl', storedUrl)
  if (storedUrl === null) {
    console.error('storedUrl is null')
    return false
  }
  if (!storedUrl.startsWith(STARTS)) {
    console.error(`storedUrl <${storedUrl}> doesn't start with <${STARTS}>.`)
    return false
  }
  return true
}

export function getStreamlabsToken() {
  return new Promise<string>((resolve, reject) => {
    if (isValidUrl()) {
      const url = JSON.parse(window.localStorage.getItem('chatboxUrl') ?? '""') as string
      resolve(last(url.split('/')))
    } else {
      reject(new InvalidChatboxUrlError('localStorage.chatboxUrl is empty or invalid.'))
    }
  })
}

let storedConfig: StreamLabsConfig | null = null
export async function fetchStreamlabsConfig() {
  if (storedConfig !== null) {
    return storedConfig
  }
  const storedUrl = JSON.parse(window.localStorage.getItem('chatboxUrl') ?? '""')
  const response = await fetch(`https://murmuring-plateau-21894.herokuapp.com/?url=${storedUrl}`, {
    headers: {
      'x-token': 'eQ1z8bmURCR2Odtg4NR8M'
    }
  })
  if (!response.ok) {
    throw new InvalidChatboxUrlError('localStorage.chatboxUrl is empty or invalid.')
  }
  const config = (await response.json()) as StreamLabsConfig
  storedConfig = config
  return config
}
