import { PageHeader, Button, Tag, Typography, Row } from 'antd';
import './VideoHeader.css'
import React, { FC } from 'react';
import { LiveVideo } from '../definitions';
import { useHistory } from 'react-router-dom';
import { DateTime } from 'luxon';

const { Paragraph } = Typography;

const Content = ({ children, extraContent }: any) => {
  return (
    <Row>
      <div style={{ flex: 1 }}>{children}</div>
      <div className="image">{extraContent}</div>
    </Row>
  );
};

export interface VideoHeaderProps {
  liveVideo: LiveVideo
}

export const VideoHeader: FC<VideoHeaderProps> = (props) => {
  const { liveVideo } = props
  const history = useHistory()
  const videoTime = DateTime.fromISO(liveVideo.creation_time)
  return (
    <PageHeader
      title={liveVideo.title}
      onBack={() => history.goBack()}
      className="site-page-header"
      subTitle={videoTime.toLocaleString(DateTime.DATETIME_MED)}
      tags={<Tag color="blue">{liveVideo.status}</Tag>}
      extra={[
        <Button key="1" type="primary" target="_blank" href={`https://facebook.com/${liveVideo.permalink_url}`}>
          Open
      </Button>
      ]}
      avatar={{ src: liveVideo.video.icon }}
    >
      <Content
        extraContent={
          <img
            src={liveVideo.video.picture}
            alt={liveVideo.title}
            width="100%"
          />
        }
      >
        <Paragraph>
          {liveVideo.description}
        </Paragraph>
      </Content>
    </PageHeader>
  )
}