import React, { FC, useContext, useEffect, useState } from 'react'
import { Card, Col, List, Row, Comment, Pagination, Layout, Spin, Slider } from 'antd'
import './App.css'
import { Breadcrumb } from 'antd'
import { LoadingOutlined, VideoCameraOutlined } from '@ant-design/icons'
import { LiveVideo, LiveVideos, PageToken } from './definitions'
import { reverse, sortBy, uniqBy } from 'lodash'
import { Redirect, Route, Switch, useHistory, useParams, useRouteMatch } from 'react-router-dom'
import { FbApiCall, FbParams, getAllResults, getPageToken, PersistedVideoData, usePersistedVideoData } from './util/fb'
import { batchInsertApiMessages, dbMessageToVideoComment } from './util/messages'
import { messagesDatabase } from './datastore/messagesDatabase'
import { VideoHeader } from './live_videos/VideoHeader'
import { PageContext } from './PageContext'
import { FacebookPaginatedResponse, VideoComment } from './util/fb.definitions'
import { COMMENTS_PER_PAGE, defaultVideoCustomState, formatComments, useVideoCustomState } from './live_videos/util'
import { generateNotifiesForRound, NotifiesList } from './live_videos/Notifies'
import { DateTime } from 'luxon'
const { Content } = Layout

const messagesParams = 'from{name,id,picture},message,id,created_time,like_count,live_broadcast_timestamp'
const defaultMessageParams: Omit<FbParams, 'access_token'> = {
  limit: 100,
  live_filter: 'no_filter',
  order: 'reverse_chronological',
  fields: messagesParams
}

const VideoCard: FC<{ video: LiveVideo; span?: number; onClick?: ((selected: LiveVideo) => void) | undefined }> = (props) => {
  const { video, span = 8, onClick = () => {} } = props
  const icon = video.status === 'LIVE' ? <LoadingOutlined /> : <VideoCameraOutlined />
  return (
    <Col span={span}>
      <Card
        bordered={false}
        hoverable={video.status === 'LIVE'}
        onClick={() => onClick(video)}
        cover={<img alt={video.title} src={video.video.picture} />}
      >
        <Card.Meta title={video.title} description={video.description} avatar={icon} />
      </Card>
    </Col>
  )
}

interface HasVideos {
  videos: LiveVideo[]
}

interface CurrentVideosProp extends HasVideos {}
const VIDEOS_PER_PAGE = 6
const CurrentVideos: FC<CurrentVideosProp> = (props) => {
  const { videos } = props
  const [currentPage, setPage] = useState(1)
  const history = useHistory()
  const offset = (currentPage - 1) * VIDEOS_PER_PAGE
  const { pageId } = useParams<{ pageId: string }>()
  return (
    <Content style={{ margin: '0 16px' }}>
      <Breadcrumb style={{ margin: '16px 0' }}>
        <Breadcrumb.Item>LiaKumStreams</Breadcrumb.Item>
        <Breadcrumb.Item>Live Videos</Breadcrumb.Item>
      </Breadcrumb>
      <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
        <Row gutter={16}>
          {videos.slice(offset, offset + VIDEOS_PER_PAGE).map((v) => (
            <VideoCard key={`video-${v.id}`} video={v} onClick={(v) => history.push(`/${pageId}/live_videos/${v.id}`)} />
          ))}
        </Row>
        <Row gutter={16}>
          <Pagination
            current={currentPage}
            onChange={(p) => setPage(p)}
            total={videos.length}
            showSizeChanger={false}
            defaultPageSize={VIDEOS_PER_PAGE}
            defaultCurrent={1}
          />
        </Row>
      </div>
    </Content>
  )
}


async function loadCachedMessages(videoId: string) {
  const existingMessages = await messagesDatabase.messages.where({ videoId: videoId }).toArray()
  return existingMessages.map(dbMessageToVideoComment)
}

interface CommentsListProps {
  comments: VideoComment[]
  perPage: number
  text: string
}
const CommentsList: FC<CommentsListProps> = (props) => {
  const { comments, perPage, text } = props
  const [currentPage, setPage] = useState(1)
  const offset = (currentPage - 1) * perPage
  return (
    <>
      <List
        className="comment-list"
        header={`${comments.length} ${text}`}
        itemLayout="horizontal"
        dataSource={formatComments(comments.slice(offset, offset + perPage))}
        renderItem={(item) => (
          <li>
            <Comment actions={item.actions} author={item.author} avatar={item.avatar} content={item.content} datetime={item.datetime} />
          </li>
        )}
      />
      <Pagination
        current={currentPage}
        onChange={(p) => setPage(p)}
        total={comments.length}
        showSizeChanger={false}
        defaultPageSize={COMMENTS_PER_PAGE}
        showTotal={(total, range) => `${range[0]}-${range[1]} of ${total} ${text}`}
        defaultCurrent={1}
      />
    </>
  )
}

function printRoundLine(name: string, round: number, roundSize: number) {
  return `${round % roundSize === 0 ? '----Ronda----\n' : ''}${name}\n`
}

const CommentsFeed: FC<{ video: LiveVideo }> = (props) => {
  const { video } = props
  const page = useContext(PageContext) as PageToken
  const [loaded, setLoaded] = useState(false)
  const [roundSize, setRoundSize] = useState(5)
  const [comments, setComments] = useState<VideoComment[]>([])
  const [videoState] = useVideoCustomState(video.id)(defaultVideoCustomState)
  const { pageId } = useParams<{ pageId: string }>()
  useEffect(() => {
    let since = new Date().toISOString()
    const effect = async () => {
      if (video.status !== 'LIVE') {
        setComments(sortBy(await loadCachedMessages(video.id), (m) => DateTime.fromISO(m.created_time).toMillis()).reverse())
        setLoaded(true)
        return
      }
      let comments = await FbApiCall<FacebookPaginatedResponse<VideoComment>>(`${video.id}/comments`, {
        ...defaultMessageParams,
        access_token: page.access_token
      })
      const allResults = await getAllResults(comments)
      setComments(allResults)
      if (allResults.length > 0) {
        since = allResults[0].created_time
      }
      setLoaded(true)
      return
    }
    const interval = setInterval(async () => {
      const newComments = await FbApiCall<FacebookPaginatedResponse<VideoComment>>(`${video.id}/comments`, {
        ...defaultMessageParams,
        since,
        access_token: page.access_token
      })
      if (newComments.data.length > 0) {
        since = newComments.data[0].created_time
      }
      setComments((prev) => uniqBy([...newComments.data, ...prev], 'id'))
    }, 5000)
    effect()
    return () => clearInterval(interval)
  }, [loaded, video.id, pageId, video.status, page.access_token])

  const notifies = reverse(
    uniqBy(
      comments.filter((c) => c.message.includes('!') && c.message.toLowerCase().includes('notify') && (c.from.name !== page.name ?? 'UNKNOWN PAGE')),
      (c) => c.from.name
    )
  )

  const filteredNotifies = generateNotifiesForRound(video.id, notifies, videoState)

  return (
    <>
      <Col span={8}>
        <Card title="Comments">
          <CommentsList text="replies" comments={comments} perPage={COMMENTS_PER_PAGE} />
        </Card>
      </Col>
      <Col span={8}>
        <Card title="Notifies">
          <NotifiesList text="notifies" comments={notifies} perPage={COMMENTS_PER_PAGE} />
        </Card>
      </Col>

      <Col span={8}>
        <Card title="Rounds by Notifies">
          Size: <Slider defaultValue={5} tooltipVisible value={roundSize} onChange={(v: number | any) => typeof v === 'number' && setRoundSize(v)} />
          <pre>{filteredNotifies.map((n, i) => printRoundLine(n.from.name, i, roundSize))}</pre>
        </Card>
      </Col>
    </>
  )
}

interface LiveVideoDashboardProps extends HasVideos {}
const LiveVideoDashboard: FC<LiveVideoDashboardProps> = (props) => {
  const page = useContext(PageContext) as PageToken
  const [persistedVideoData, setPersistedVideoData] = usePersistedVideoData<PersistedVideoData>({})
  const [loading, setLoading] = useState(true)
  const { id } = useParams<{ id: string }>()
  const { videos } = props
  const video = videos.find((v) => v.id === id)

  useEffect(() => {
    const effect = async () => {
      if (video !== undefined && persistedVideoData[video.id] === true) {
        setLoading(false)
        return
      }
      if (video === undefined || video.status === 'LIVE') {
        setLoading(false)
        return
      }

      let comments = await FbApiCall<FacebookPaginatedResponse<VideoComment>>(`${video.id}/comments`, {
        ...defaultMessageParams,
        access_token: page.access_token
      })
      const allResults = await getAllResults(comments)
      await batchInsertApiMessages(allResults, video.id, page.id)
      setPersistedVideoData((prev) => ({ ...prev, [video.id]: true }))
      setLoading(false)
    }
    effect()
  }, [loading, setPersistedVideoData, persistedVideoData, video, page.access_token, page.id])

  if (video === undefined) {
    return <Redirect to="/live_videos" />
  }
  return (
    <Content style={{ margin: '0 16px' }}>
      <Breadcrumb style={{ margin: '16px 0' }}>
        <Breadcrumb.Item>LiaKumStreams</Breadcrumb.Item>
        <Breadcrumb.Item>Live Videos</Breadcrumb.Item>
        <Breadcrumb.Item>{video.title}</Breadcrumb.Item>
      </Breadcrumb>

      <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
        <Row gutter={16}>
          <VideoHeader liveVideo={video} />
        </Row>
        <Row gutter={16}>{loading ? <Spin /> : <CommentsFeed video={video} />}</Row>
      </div>
    </Content>
  )
}

export const LiveVideosDashboard: FC = () => {
  const { pageId } = useParams<{ pageId: string }>()
  const [loadingVideos, setLoadingVideos] = useState(true)
  const [videos, setVideos] = useState<LiveVideo[]>([])
  const [pageData, setPageData] = useState<PageToken>()
  useEffect(() => {
    if (loadingVideos === true) {
      const effect = async () => {
        const tokenItem = await getPageToken(pageId)
        FB.api(
          `/${pageId}/live_videos`,
          'get',
          {
            access_token: tokenItem?.access_token,
            fields: 'status,creation_time,broadcast_start_time,description,embed_html,id,live_views,title,permalink_url,video{id,picture,icon}'
          },
          function (response: LiveVideos) {
            setLoadingVideos(true)
            setVideos(response.data)
            setPageData(tokenItem)
            setLoadingVideos(false)
          }
        )
      }
      effect()
    }
  }, [loadingVideos, pageId])
  let match = useRouteMatch()
  return (
    <PageContext.Provider value={pageData}>
      <Switch>
        <Route path={`${match.path}/:id`}>{loadingVideos ? <Spin size="large" /> : <LiveVideoDashboard videos={videos} />}</Route>
        <Route path={match.path}>{loadingVideos ? <Spin size="large" /> : <CurrentVideos videos={videos} />}</Route>
      </Switch>
    </PageContext.Provider>
  )
}
