import { SettingOutlined } from '@ant-design/icons'
import { Breadcrumb, Button, Form, Input, Layout, Modal } from 'antd'
import React, { FC, useState } from 'react'
import { useChatBoxUrlState } from './util/streamlabs'

interface Values {
  chatboxUrl: string
}

interface CollectionCreateFormProps {
  visible: boolean
  onCreate: (values: Values) => void
  onCancel: () => void
}

const CollectionCreateForm: React.FC<CollectionCreateFormProps> = ({ visible, onCreate, onCancel }) => {
  const [chatboxUrl] = useChatBoxUrlState<string>()
  const [form] = Form.useForm<Values>()
  return (
    <Modal
      visible={visible}
      title="Set Chatbox URL"
      okText="Set"
      cancelText="Cancel"
      onCancel={onCancel}
      onOk={() => {
        form
          .validateFields()
          .then((values) => {
            form.resetFields()
            onCreate(values as Values)
          })
          .catch((info) => {
            console.log('Validate Failed:', info)
          })
      }}
    >
      <Form form={form} layout="vertical" name="form_in_modal" initialValues={{ chatboxUrl: chatboxUrl ?? '' }}>
        <Form.Item name="chatboxUrl" label="Chatbox URL" rules={[{ required: true, message: 'Please input the chatbox url!' }]}>
          <Input addonAfter={<SettingOutlined />} placeholder="https://streamlabs.com/widgets/chat-box/v1/XXXXXXXXXXXXXXXXXXXX" />
        </Form.Item>
      </Form>
    </Modal>
  )
}

const UpdateChatboxUrl = () => {
  const [visible, setVisible] = useState(false)
  const [chatboxUrl, setChatboxUrl] = useChatBoxUrlState<string>()

  const onCreate = (values: Values) => {
    console.log('Received values of form: ', values)
    setChatboxUrl(values.chatboxUrl)
    setVisible(false)
  }

  return (
    <>
      <Button
        type="primary"
        onClick={() => {
          setVisible(true)
        }}
      >
        Update Chatbox URL
      </Button>
      {chatboxUrl}
      <CollectionCreateForm
        visible={visible}
        onCreate={onCreate}
        onCancel={() => {
          setVisible(false)
        }}
      />
    </>
  )
}

const { Content } = Layout

export const PageSelector: FC = () => {
  return (
    <Content style={{ margin: '0 16px' }}>
      <Breadcrumb style={{ margin: '16px 0' }}>
        <Breadcrumb.Item>Home</Breadcrumb.Item>
      </Breadcrumb>
      <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
        <UpdateChatboxUrl />
      </div>
    </Content>
  )
}
