import { Tooltip } from "antd"
import React from "react"
import createPersistedState from "use-persisted-state"
import { memoize } from "lodash"
import { DateTime } from "luxon"
import { VideoComment } from "../util/fb.definitions"

export function formatComments(comments: VideoComment[]) {
  return comments.map((c) => {
    const date = DateTime.fromISO(c.created_time)
    return {
      actions: [],
      author: c.from.name,
      avatar: c.from.picture.data.url,
      content: <p>{c.message}</p>,
      datetime: (
        <Tooltip title={date.toLocaleString(DateTime.TIME_24_WITH_SECONDS)}>
          <span>{date.toRelative()}</span>
        </Tooltip>
      )
    }
  })
}
export interface CustomVideoState {
  messages: Record<string, CustomMessageState>
}
export interface CustomMessageState {
  hidden: boolean
  down: boolean
}
export const defaultVideoCustomState: CustomVideoState = {
  messages: {}
}
export const useVideoCustomState = memoize((videoId: string) => createPersistedState(`custom-video-settings-${videoId}`))

export const COMMENTS_PER_PAGE = 10