import Dexie from 'dexie';

export interface Message {
  id?: string;
  message?: string;
  createdTime?: string;
  broadcastTimestamp?: number;
  user?: string
  userId?: string
  userPicture?: string
  pageId?: string
  videoId?: string
  likeCount?: number
}

//
// Declare Database
//
class MessageDatabase extends Dexie {
  public messages: Dexie.Table<Message, string>; // id is number in this case

  public constructor() {
    super("MessageDatabase");
    this.version(5).stores({
      messages: "id,user,pageId,videoId,[message+user],[message+videoId],[message+userId]"
    });
    this.messages = this.table("messages");
  }
}

export const messagesDatabase = new MessageDatabase();